<script>
import qs from "qs";

export default {
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      list: {}, //顯示的資料
      emptyRules: [(v) => !!v || "不可空白"],
      newData: false,
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/hour72",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.length == 0) {
            //未建立資料
            this.newData = true;
            // this.createList();
            // this.addNew();
          } else {
            this.newData = false;
            var matchData = rs.data.map((i) => {
              if (i.datalist != "") {
                var newItem = JSON.parse(i.datalist);
                newItem.snkey = i.snkey;
                return newItem;
              }
            });
            this.list = matchData[0];
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    addNew() {
      //無資料時新增一筆資料
      this.$store.commit("getTime"); //取得現在時間
      var addData = {
        u_snkey: this.$store.state.uData.snkey,
        datalist: JSON.stringify(this.list),
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
      };

      this.axios
        .post(
          "general/add/" + this.$store.state.databaseName + "/hour72",
          qs.stringify(addData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "已儲存", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.newData = false;
            this.getAllData();
          }
        });
    },
    editOK() {
      //修改功能
      if (this.$refs.form.validate()) {
        if (this.newData) {
          console.log("run add");
          this.addNew();
        } else {
          console.log("run edit");
          this.$store.commit("getTime"); //取得現在時間
          var postData = {
            snkey: this.list.snkey,
            datalist: JSON.stringify(this.list),
            edit_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //執行動作的人,時間,內容
          };

          this.axios
            .post(
              "general/edit/" + this.$store.state.databaseName + "/hour72",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已儲存", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.saveNursingassessment();
              }
            });
        }
      } else {
        var pop = {
          msg: "評估日期不可為空白",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    print() {
      // sessionStorage.setItem("printData",JSON.stringify(this.checkedNutritionfilter));

      let routeData = this.$router.resolve({path: "/print/hour72print",query:{data:JSON.stringify(this.list)}});
      window.open(routeData.href, "_blank");
    },
  },
};
</script>


